import React from "react";
import "./ShowingApi.css";
const ShowingApi = ({ data }) => {
  return (
    <div>
      <h1>Movies Details</h1>
      <div className="parent-container">
        {data.movies.map((m) => {
          return (
            <div className="movies-container">
              <p>{m.name}</p>
              <p>{m.rating}</p>
              <p>{m.releasedDate}</p>
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default ShowingApi;
